import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'

import Designcun from '../components/Designcun.vue'
import Welcome from '../components/Welcome.vue'
import Codecun from '../components/Codecun.vue'
import Productcun from '../components/Productcun.vue'


Vue.use(VueRouter)

const routes = [
  {
    path: '/home',
    name: 'Home',
    component: Home,
    redirect: '/home/designcun',
    children: [
               {path: 'welcome', component: Welcome, name: 'Welcome'},
               {path: 'designcun', component: Designcun, name: 'Designcun'},
               {path: 'codecun', component: Codecun, name: 'Codecun'},
               {path: 'productcun', component: Productcun, name: 'Productcun'},
  ]
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },
  // {
  //   path: '/welcome',
  //   name: 'Welcome',
  //   component: Welcome,
  // }
]

const router = new VueRouter({
  routes
})

export default router
